.layout1 {
  margin-top: 8px;
}
.layout22 {
  padding-left: 0;
  padding-right: 0;
}
.homeInner {
  font-family: "Roboto", sans-serif;
  border: 1px solid #c9c9c9;
  margin-bottom: 15px;
  margin-top: 10px;
  box-shadow: 1px 1px 1px 0px #efefef;
  h2 {
    background: #2391d0 no-repeat center right;
    font-size: 14px;
    color: #fff;
    text-transform: uppercase;
    padding: 10px 15px;
  }
  a {
    font-size: 14px;
    color: #fff;
    text-transform: uppercase;
    font-weight: bold;
  }
  a:hover {
    text-decoration: none;
    // color: #ac0000;
  }
  ul {
    padding: 15px 0 15px 20px;
    list-style: square;
    margin: 0;
    li {
      margin-bottom: 12px;
    }
    a {
      font-size: 14px;
      color: #222;
      text-transform: none;
      font-weight: normal;
    }
    a:hover {
      text-decoration: none;
      color: #ac0000;
    }
  }
}
.title_video_block {
  h2 {
    margin-bottom: 10px;
    margin-top: 10px;
  }
  a {
    text-transform: uppercase;
    font-weight: normal;
    color: #333333;
    font-family: "Roboto", sans-serif;
    line-height: 22px;
    font-size: 14px;
    font-weight: bold;
  }
  a:hover {
    text-decoration: none;
  }
}
.ideo_gallery {
  font-family: "Roboto", sans-serif;
  border: 1px solid #c9c9c9;
  margin-bottom: 15px;
  padding: 8px;
  box-shadow: 1px 1px 1px 0px #efefef;
  position: relative;
  h3 {
    color: #2391d0;
    margin-top: 10px;
    font-size: 14px;
    font-weight: bold;
  }
  .video {
    height: auto;
    max-width: 100%;
    text-align: center;
  }
  a {
    text-transform: uppercase;
    // color: #333333;
    img {
      height: auto;
      max-width: 100%;
      text-align: center;
    }
  }
  a:hover {
    text-decoration: none;
  }
}
.banner_list {
  font-family: "Roboto", sans-serif;
  line-height: 22px;
  color: #333333;
  a {
    color: #222;
    text-decoration: none;
    
  }
  .banners_item{
    img {
      margin: 0 auto;
      margin-bottom: 5px;
      height: auto;
      max-width: 100%;
      text-align: center;
      height: 100px;
      width: 100%;
    }
    .newsTitle {
      font-size: 14px;
      font-weight: 500;
      margin-bottom: 16px;
      flex: 1;
      // padding-left: 16px
    }
  }
}
