.btn-success {
  background-color: #129c4f;
  border-color: #129c4f;
  padding-left: 25px;
  padding-right: 25px;
  font-size: 14px !important;
  font-weight: 500;
}
.modal-dialog {
  @media(min-width: 576px) {
    max-width: 576px;
  }
}
.btn-outline-success, .btn-success {
  &:hover {
    img {
      filter: contrast(0%) brightness(1050%);
    }
  }
}
.btn-outline-success {
  &:hover {
    background-color: #129c4f;
    border-color: #129c4f;
  }
}
.btn:focus {
  box-shadow: none !important
}
.col-20 {
  width: 20%;
}
