.item {
  opacity: 1;
  position: relative;
  .detail {
    visibility: hidden;
    position: absolute;
    top: 0;
    left: 15px;
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0,0.5);
    color: white;
  }
  .detail1 {
    visibility: visible;
  }
  &:hover>.detail {
    visibility: visible;
  }
  &:hover>.detail1 {
    visibility: hidden;
  }
}
.cate {
  cursor: pointer;
  &:hover {
    text-decoration: underline;
  }
}
