.btn-trans {
  background-color: transparent;
  color: white;
  border: none;
  flex-grow: 1;
  text-align: left;
  font-size: 0.75rem;
  &:hover {
    background-color: transparent;
  }
  &:focus {
    background-color: transparent;
  }
}
.side-t {
  cursor: pointer;
  &:hover {
    text-decoration: underline;
  }
}

.menu-custom {
  padding: 8px 0 !important;
}

.menu-sub-menu {
  padding: 8px 12px !important;
  font-weight: 600;
}