.duocct-content {
  box-shadow: 4px 4px 4px 4px rgba(0, 0, 0, 0.08);
  margin-top: 2rem;
  > button {
    width: 100%;
    text-align: left;
    border: none;
    border-radius: 0px;
    padding: 0.75rem 0.75rem;
  }
  .collapse {
    padding: 0.375rem 0.75rem;
    background-color: #8a979f;
  }
}
.tintuc {
  cursor: pointer;
  &:hover {
    text-decoration: underline;
  }
}

.btn-send {
  width: 100%;
  height: 50px;
}

.map {
  margin-top: 40px;
  margin-bottom: 40px;
}

.customInput {
  height: 45px;
}

.mb10 {
  margin-bottom: 10px;
}

.mb40 {
  margin-bottom: 40px !important;
}