.error {
  font-size: 11px;
  color: red;
}
.submit-btn {
  background-color: #129c4f;
  font-weight: 500;
  font-size: 16px;
  box-shadow: 0 10px 20px 0 rgba(18, 156, 79, 0.1);
  padding: 5px 23px;
  &:hover {
    opacity: 0.7;
  }
}
.new-field {
  position: relative;
  > label {
    position: absolute;
    top: 13px;
    left: 8px;
    z-index: 10;
    background-color: #fff;
    margin: 0;
    padding-left: 5px;
    padding-right: 5px;
    white-space: nowrap;
    line-height: 1;
    font-weight: 600;
    font-size: 12px;
  }
  input {
    font-size: 14px;
    background-color: #fff;
  }
  input:disabled {
    // background-color: white;
    border: none;
    cursor: not-allowed;
    background-image: linear-gradient(to bottom, var(--white), #f4f4f4 96%);
    border: solid 1px #dde2e4;
  }
  input:read-only {
    // background-color: white;
    border: none;
    background-image: linear-gradient(to bottom, var(--white), #f4f4f4 96%);
    border: solid 1px #dde2e4;
    // cursor: not-allowed;
  }
}
