.carousel-caption {
  background-color: rgba(0, 0, 0, 0.6);
  padding: 20px;
  display: block !important;
  left: 15px;
  right: 15px;
  border-radius: 20px 20px 0 0;
  p {
    text-align: left;
    font-size: 24px;
    font-weight: 500;
  }
}
.item-sec1 {
}
.item-img {
  max-width: 100%;
}
.section2 {
  background-color: #f5f5f5;
}
.section3 {
  padding: 100px 0 !important;
}
.dvc {
  background-color: #f4f4f4;
  cursor: pointer;
  padding: 8px 20px !important;
  &:hover {
    background-color: #e5e5e5;
  }
}

.section3-content {
  background-color: #f4f4f4;
  height: calc(100% - 41px);
}

.news-title {
  font-size: 14px;
  font-weight: 600;
  color: #333333;
  margin-bottom: 8px;
  @media (max-width: 767px) {
    margin-top: 16px;
  }
}
.news-date {
  font-size: 12px;
  color: #666666;
}

.section-head {
  padding: 10px 20px !important;
}

.custom-icon {
  width: 20px;
}

.viewall {
  margin-bottom: 0;
  text-align: right;
  font-size: 14px;
  color: #2391d0;
  cursor: pointer;
  font-weight: 600;
  &:hover {
    text-decoration: underline;
  }
}

.row-between {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 16px;
  .news-new {
    font-size: 20px;
    color: #333333;
    margin-bottom: 0;
    font-weight: bold;
    text-transform: uppercase;
  }
}


.section0 {
  padding: 60px 0 100px 0;
}
