.duocct-content {
  box-shadow: 4px 4px 4px 4px rgba(0, 0, 0, 0.08);
  margin-top: 2rem;
  > button {
    width: 100%;
    text-align: left;
    border: none;
    border-radius: 0px;
    padding: 0.75rem 0.75rem;
  }
  .collapse {
    padding: 0.375rem 0.75rem;
    background-color: #8a979f;
  }
}
.tintuc {
  cursor: pointer;
  &:hover {
    text-decoration: underline;
  }
}

.to-home-content {
  cursor: pointer;
  font-size: 20px;
  color: #2391d0;
}
.link-page {
  cursor: pointer;
  font-size: 15px;
  font-weight: 500;
  &:hover {
    text-decoration: underline;
  }
}