.row {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
  .image {
    width: 230px;
    margin-right: 20px;
  }
  .info {
    flex: 1
  }
  .name {
    font-size: 14px;
    color: #363636;
    margin-bottom: 13px;
    strong {
      text-transform: uppercase;
      margin-left: 4px;
    }
  }

  .position {
    font-size: 14px;
    color: #363636;
    font-weight: bold;
    margin-bottom: 13px;
  }

  .description {
    text-align: justify;
    font-size: 14px;
    line-height: 2.14;
  }
}