.cate {
  border: 1px solid #2391d0;
  border-radius: 10px;
  cursor: pointer;
  padding: 8px 16px !important;
  color: #2391d0;
  font-weight: 600;
  
}

.categoryWrapper {
  padding: 0 5px;
  margin-top: 10px;
  margin-bottom: 10px !important;
}

.catActive {
  background-color: #2391d0;
  color: #ffffff;
}

.container {
  margin-top: 20px;
  padding: 0 30px;
}

:global {
  .slick-prev:before, .slick-next:before {
    color: #2391d0 !important;
  }
}