  background-color: #2391d0;
  @media (max-width: 767px) {
    max-height: 100vh;
    overflow-y: auto;
  }

}

.banner {
  margin-top: 75px;
  padding: 0;
  position: relative;
  background-image: url('../_assets/bg-hoatdong.jpg');
  height: 550px;
  @media (max-width: 768px) {
    height: 850px;
  }
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  width: 100%;
  margin-bottom: 40px;
  &:before {
    position: absolute;
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    opacity: .7;
    background-image: linear-gradient(
      119deg
      ,#000000 15%,#000 56%,#000 99%);
    }

  .inputWrapper {
    position: absolute;
    top: 50px;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 9;
    .searchIcon {
      width: 20px;
      position: absolute;
      top: 50%;
      left: 15px;
      transform: translateY(-50%);
      z-index: 10;
      opacity: 0.6;
    }
  }
  .video-2 {
    position: absolute;
    top: 90px;
    left: 50%;
    transform: translate(-50%, 0%);
    z-index: 19;
    width: 100%;
  }
  .input {
    max-width: 800px;
    height: 50px;
    width: 800px;
    border-radius: 25px !important;
    padding: 0 50px 0 50px;
    border: 1px solid #ebebeb;
    outline: 0;

  }

  @media (max-width: 767px) {
    .input {
      width: 100%;
    }
    .inputWrapper {
      left: 20px;
      right: 20px;
      top: 20px;
      transform: translateX(0);
    }
  }
}

