.table {
  width: calc(100%);
  background-color: #ffffff;
  table {
    margin-bottom: 0;
    width: 100%;
    border-collapse: collapse;
  }
  // thead {
  //   position: sticky;
  //   top: 60px;
  // }
  .tbHead {
    background-color: rgba(9,38,49, 0.05);

    th {
      font-size: 13px;
      font-weight: 700;
      line-height: 1.27;
      text-transform: uppercase;
      padding: 20px;
      vertical-align: top;
      min-width: 100px;
      color: #333333;
      cursor: pointer;
      position: relative;
      white-space: nowrap;
      border: 1px solid rgba(9,38,49,0.1);
    }
    .icon {
      vertical-align: top;
      margin-left: 10px;
      opacity: 0.3;
    }
    .iconActive {
      opacity: 1;
    }
    .unit {
      margin-bottom: 0;
      font-size: 12px;
      color: #919b8e;
      text-align: center;
      font-weight: normal;
      margin-top: 4px;
      text-align: right;
    }
  }
  .tbRow {

    cursor: pointer;
    td {
      border: 1px solid rgba(9,38,49,0.1);
      font-size: 15px;
      line-height: 1.54;
      padding: 17px 20px;
      font-weight: 500;
      color: #333333;
    }

    &:hover {
      // box-shadow: 3px 3px 6px 0 rgba(0, 0, 0, 0.16);
      // border: solid 1px #f7f9fb;
      background-color: #f7f9fb;
    }
  }

  .mw250 {
    max-width: 250px;
  }
  .mw300 {
    max-width: 300px;
  }
}
