.timeline {
  max-width: 900px;
  margin: 0 auto;
  position: relative;
  width: 100%;
  &:before {
    position: absolute;
    height: 100%;
    width: 1px;
    left: 50%;
    top: 10px;
    content: "";
    background: linear-gradient(to bottom,#46bdf4 0%,#2391d0 100%);
  }
  .row {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    align-items: flex-start;
  }
  .colLeft {
    flex: 1;
    text-align: right;
    padding: 0 25px;
  }
  .colRight {
    flex: 1;
    padding: 0 25px;
    .content {
      &:before {
        left: -35px;
        right: auto;
      }
      &:after {
        left: -35px;
        right: auto;
      }
    }
  }
  .content {
    position: relative;
    &:before {
      position: absolute;
      height: 19px;
      width: 19px;
      border: 1px solid #2391d0;
      background: #2391d0;
      top: 2px;
      border-radius: 50%;
      content: "";
      right: -35px;
      left: auto;
    }
    &:after {
      position: absolute;
      content: "\2713";
      top: 2px;
      width: 19px;
      height: 19px;
      border-radius: 50%;
      background: 0 0;
      font-size: 10px;
      line-height: 19px;
      text-align: center;
      right: -35px;
      left: auto;
      opacity: 1;
      color: #fff;
    }

    h6 {
      margin-bottom: 8px;
      color: #2391d0;
      line-height: 1.33;
      font-weight: 600;
      font-size: 14px;
    }
    .title {
      margin-top: 35px;
      margin-bottom: 18px;
      color: #2391d0;
      line-height: 1.33;
      font-weight: bold;
      font-size: 20px;
    }
    .description {
      font-size: 15px;
      line-height: 1.86;
      color: #000000;
    }
  }

  @media (max-width: 767px) {
    &:before {
      left: 12px
    }
    .row {
      flex-direction: column;
      padding: 0 25px 0 40px;
      margin-bottom: 24px;
    }
    .colLeft {
      padding: 0;
      text-align: left;
      .content {
        &:before {
          left: -35px;
          right: auto;
        }
        &:after {
          left: -35px;
          right: auto;
        }
      }
    }
    .colRight {
      padding: 0;
    }
    
  }
}

.largeTitle {
  text-align: center;
  margin-bottom: 24px;
  font-weight: bold;
}

.about-menus {
  width: 100%;
  margin: 0 auto;
}