.container {
  --d: 700ms;
  --e: cubic-bezier(.19,1,.22,1);
  margin: 60px 0;
  position: relative;
  .content {
    max-width: 1440px;
    margin: 0 auto;
    width: 100%;
    text-align: center;
    padding: 0 12px;
  }


  .marketplaces {
    display: flex;
    flex-wrap: wrap;
    max-width: 1440px;
    margin: 0 auto;
    justify-content: center;
    padding: 0 12px;
    width: 100%;
    .card {
      height: 450px;
      position: relative;
      display: flex;
      align-items: flex-end;
      overflow: hidden;
      padding: 2.5rem;
      width: 382px;
      text-align: center;
      color: #f5f5f5;
      background-color: #000;
      opacity: .9;
      box-shadow: 0 1px 1px rgba(0,0,0,0.1), 0 2px 2px rgba(0,0,0,0.1), 0 4px 4px rgba(0,0,0,0.1), 0 8px 8px rgba(0,0,0,0.1), 0 16px 16px rgba(0,0,0,0.1);
      &:before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 110%;
        background-size: cover;
        background-position: 0 0;
        transition: transform calc(var(--d) * 1.5) var(--e);
        pointer-events: none;

      }
      &:after {
        content: "";
        display: block;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 200%;
        pointer-events: none;
        background-image: linear-gradient(to bottom,rgba(212,170,61,0) 0%,rgba(212,170,61,.009) 11.7%,rgba(212,170,61,.034) 22.1%,rgba(212,170,61,.072) 31.2%,rgba(212,170,61,.123) 39.4%,rgba(212,170,61,.182) 46.6%,rgba(212,170,61,.249) 53.1%,rgba(212,170,61,.32) 58.9%,rgba(212,170,61,.394) 64.3%,rgba(212,170,61,.468) 69.3%,rgba(212,170,61,.54) 74.1%,rgba(212,170,61,.607) 78.8%,rgba(212,170,61,.668) 83.6%,rgba(212,170,61,.721) 88.7%,rgba(212,170,61,.762) 94.1%,rgba(212,170,61,.79) 100%);
        transform: translateY(0);
        transition: transform calc(var(--d) * 2) var(--e);
      }
      &:nth-child(1) {
        &::before {
          background-image: url('../../../_assets/ydct.jpg');
          background-position: center;
        }
      }
      &:nth-child(2) {
        &::before {
          background-image: url('../../../_assets/dct.png');
          background-position: center;
        }
      }
      &:nth-child(3) {
        &::before {
          background-image: url('../../../_assets/cocq.jpg');
          background-position: center;
        }
      }
      &:nth-child(4) {
        &::before {
          background-image: url('../../../_assets/management.jpg');
          background-position: center;
        }
      }
      &:nth-child(5) {
        &::before {
          background-image: url('../../../_assets/htqt.png');
          background-position: center;
        }
      }
      &:nth-child(6) {
        &::before {
          background-image: url('../../../_assets/quanlycl.png');
          background-position: center;
        }
      }
      .cardConent {
        position: relative;
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 100%;
        padding: 1rem;
        transition: transform var(--d) var(--e);
        z-index: 1;
        transform: translateY(calc(100% - 4.5rem));
        .cardTitle {
          font-size: 1.6rem;
          font-weight: bold;
          line-height: 1.2;
          text-align: center;
          color: #f5f5f5;
        }
        .cardText {
          text-align: center;
          color: #f5f5f5;
          font-size: 1.125rem;
          font-style: italic;
          line-height: 1.35;
          opacity: 0;
          transform: translateY(1rem);
          transition: transform var(--d) var(--e) , opacity var(--d) var(--e);

        }
        .btn {
          cursor: pointer;
          font-size: .65rem;
          font-weight: bold;
          letter-spacing: .025rem;
          text-transform: uppercase;
          color: #fff;
          border: none;
          padding: 12px 20px 8px 13px;
          line-height: 1.5;
          text-align: center;
          opacity: 0;
          transform: translateY(1rem);
          transition: transform var(--d) var(--e) , opacity var(--d) var(--e);
          display: inline-block;
          &:after {
            bottom: 0;
            content: "";
            display: block;
            height: 2px;
            left: 50%;
            position: absolute;
            background: #fff;
            transition: width .3s ease 0s , left .3s ease 0s;
            width: 0;
          }

          &:hover {
            &:after {
              width: 100%;
              left: 0;
            }
          }
        }
      }

      &:hover {
        align-items: center;
        &:before {
          transform: translateY(-4%);
        }
        &:after {
          transform: translateY(-50%);
        }
        .cardConent {
          transform: translateY(0);
          .cardText {
            opacity: 1;
            transform: translateY(0);
            transition-delay: calc(var(--d) / 8);
          }
          .btn {
            opacity: 1;
            transform: translateY(0);
            transition-delay: calc(var(--d) / 8);
          }
        }
      }
    }
  }

  @media (max-width: 992px) {
    .title {
      font-size: 32px;
    }
    .marketplaces {
      padding-top: 0;
      .card {
        &:before {
          transform: translateY(-4%);
        }
        &:after {
          transform: translateY(-50%);
        }
        .cardConent {
          transform: translateY(0);
          .cardText {
            opacity: 1;
            transform: translateY(0);
            transition-delay: calc(var(--d) / 8);
          }
          .btn {
            opacity: 1;
            transform: translateY(0);
            transition-delay: calc(var(--d) / 8);
          }
        }
      }
    }
  }

  @media (max-width: 767px) {
    margin-bottom: 0;
    .title {
      font-size: 32px;
    }
    .marketplaces {
      padding-top: 0;
      .card {
        height: auto;
        align-items: center;
        &:before {
          transform: translateY(-4%);
        }
        &:after {
          transform: translateY(-50%);
        }
        .cardConent {
          transform: translateY(0);
          .cardText {
            opacity: 1;
            transform: translateY(0);
            transition-delay: calc(var(--d) / 8);
          }
          .btn {
            opacity: 1;
            transform: translateY(0);
            transition-delay: calc(var(--d) / 8);
          }
        }
      }
    }
  }
}
