.duocct-content {
  box-shadow: 4px 4px 4px 4px rgba(0, 0, 0, 0.08);
  margin-top: 2rem;
  > button {
    width: 100%;
    text-align: left;
    border: none;
    border-radius: 0px;
    padding: 0.75rem 0.75rem;
  }
  .collapse {
    padding: 0.375rem 0.75rem;
    background-color: #8a979f;
  }
}
.to-home {
  color: #2391d0;
  // cursor: pointer;
}
.to-home-content {
  cursor: pointer;
  font-size: 20px;
  color: #2391d0;
}
.link-page {
  cursor: pointer;
  font-size: 15px;
  font-weight: 500;
  &:hover {
    text-decoration: underline;
  }
}
.cate {
  border: 1px solid #2391d0;
  border-radius: 10px;
  cursor: pointer;
  padding: 8px 16px !important;
  color: #2391d0;
  font-weight: 600;
  margin-top: 10px;
  margin-bottom: 10px !important;
  margin-right: 10px !important;
}

.cat-active {
  background-color: #2391d0;
  color: #ffffff;
}

.inputField {
  height: 50px;
  border-radius: 25px !important;
  border: 1px solid #ebebeb;
  padding: 0 25px;
}

.input-wrapper {
  position: relative;
  .btnSearch {
    width: 40px;
    height: 40px;
    background-color: #2391d0;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50% !important;
    z-index: 99;
    .searchIcon {
      width: 20px;
    }
    position: absolute;
    right: 5px;
    top: 5px;
  }
}

.no-news {
  display: block;
}

.documentLink {
  display: block;
  margin-bottom: 8px;
}

.documentTitle {
  margin-bottom: 20px;
  display: block;
}