.videoWrapper {
  margin-top: 18px;
  position: relative;
  padding-top: 56.25%;
  .video {
    position: absolute;
    top: 0;
    left: 0;
  }
}

.thumbVideoWrapper {
  width: 100px;
  pointer-events: none;
  position: relative;
  .thumbVideo {
    position: relative;
    padding-top: 56.25%;

    .video {
      position: absolute;
      top: 0;
      left: 0;
    }
  }
}



.container {
  display: flex;
  align-items: flex-start;
  margin-bottom: 100px;
  .left {
    flex: 1;
    margin-right: 20px;
  }
  .news {
    width: 640px;
    max-width: 100%;
  }
  .title {
    font-size: 24px;
    font-weight: 600;
    margin-top: 15px;
    margin-bottom: 0;
    width: 100%;
  }


  .new {
    display: flex;
    align-items: center;
    cursor: pointer;
    padding: 10px;
    .newsTitle {
      font-size: 14px;
      font-weight: 500;
      margin-bottom: 0;
      flex: 1;
      padding-left: 16px
    }


    .playing {
      display: none;
    }
  }

  .newActive {
    background-color: #f5f5f5;
    border-radius: 5px;
    .playing {
      display: block;
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      z-index: 2;
      background-color: rgba(0,0,0,0.8);
      display: flex;
      align-items: center;
      justify-content: center;
      font-size: 14px;
      color: #ffffff;
    }
  }

  @media (max-width: 767px) {
    flex-direction: column;
    .left {
      width: 100%;
      margin-right: 0;
    }
    .news {
      width: 100%;
    }
  }
}
