.duocct-content {
  box-shadow: 4px 4px 4px 4px rgba(0, 0, 0, 0.08);
  margin-top: 2rem;
  > button {
    width: 100%;
    text-align: left;
    border: none;
    border-radius: 0px;
    padding: 0.75rem 0.75rem;
  }
  .collapse {
    padding: 0.375rem 0.75rem;
    background-color: #8a979f;
  }
}
.tintuc {
  cursor: pointer;
  &:hover {
    text-decoration: underline;
  }
}
.history {
  background-image: url('../Home/section2/bg-hoatdong.jpg');
  background-repeat: no-repeat;
  background-attachment: fixed
}
.history-1 {
  background-color: rgba(0,0,0,0.6)
}
.left {
  border-right: solid 1px #16a2f2;
  position: relative;
}

.right {
  position: relative;
}
.cc {
  width: 90%;
  box-shadow: 0px 1px 7.5px 0 rgba(0, 0, 0, 0.12);

  b {
    color: #16a2f2;
  }
}
.line {
  width: 10%;
  border-top: solid 1px #16a2f2;
  margin-top: 2.5%;
}
.dot {
  width: 5%;
  height: 5%;
  border-radius: 50%;
  background-color: #16a2f2;
  position: absolute;
    right: -2.5%;
}
.dot1 {
  width: 5%;
  height: 5%;
  border-radius: 50%;
  background-color: #16a2f2;
  position: absolute;
    left: -2.5%;
}
.pt-10 {
  padding-top: 6rem;
}


.about-container {
  margin-top: 76px;
}

.thanh-tich-car-body {
  padding: 0 !important;
}

.videoWrapper {
  margin-top: 18px;
  position: relative;
  padding-top: 56.25%;
  .video {
    position: absolute;
    top: 0;
    left: 0;
  }
}

.about-menus {
  border-bottom: 1px solid #ebebeb;
  display: flex;
  margin-bottom: 0;
  margin-top: 100px;
  .about-menu {
    padding: 20px;
    display: block;
    font-size: 15px;
    font-weight: 500;
    color: #333333;
    &:hover {
      color: #2391d0;
      text-decoration: none;
    }
  }

  .about-menu-active {
    color: #2391d0;
    font-weight: bold;
    position: relative;
    &:after {
      position: absolute;
      width: 100%;
      height: 3px;
      background-color: #2391d0;
      content: '';
      position: absolute;
      left: 0;
      bottom: 0;
    }
  }
}