.pagination {
  text-align: right;
  ul {
    justify-content: flex-end;
  }

  .pageItem {
    margin-right: 10px;
    button {
      width: 40px;
      height: 40px;
      border-radius: 50% !important;
      border: 1px solid #ebebeb;
      display: flex;
      align-items: center;
      justify-content: center;
      span {
        font-size: 22px;
      }
    }
  }
}