.newslistContent {
  font-family: "Roboto", sans-serif;
  border: 1px solid #d8d8d8;
  margin-bottom: 10px;
  // padding: 8px 0;
}
.tabsNewsLayout {
  background: #f0f0f0;
}
.tabsNews {
  height: 38px;
  // padding: 0px 0 8px;
  background: repeat-x bottom #f0f0f0;
  border: 1px solid #fff;
  border-bottom: none;
  // margin-top: 8px;
  padding-left: 0;
  list-style: none;
}
.tabsNewsActive {
  float: left;
  // display: block;
  // height: 38px;
  padding: 8px 10px;
  border-radius: 0;
  background: #fff;
  border-top: 1px solid #ddd;

  a {
    color: #ac0000;
    border-bottom-color: transparent;
    // cursor: default;
    // padding: 8px 12px 8px;
    font-weight: bold;
    font-size: 14px;
  }
}
.tabsNewsUnActive {
  float: left;
  // display: block;
  // height: 38px;
  padding: 8px 10px;
  border-radius: 0;
  background: #f0f0f0;
  border-top: 1px solid #ddd;

  a {
    border-bottom-color: transparent;
    // cursor: default;
    color: #333333;

    // padding: 8px 12px 8px;
    border-radius: 0;
    font-weight: bold;
    font-size: 14px;
  }
}
.layoutContent2 {
  padding-left: 0px;
  // display: block;
  list-style: none;
}
.content2li {
  font-size: 13px;
  display: block;
  line-height: 18px;
  overflow: hidden;
  height: 39px;
  padding: 0 9px 9px 9px;
  a {
    color: #333333;
  }
  a:hover {
    text-decoration: none;
    color: #ac0000;
  }
}
.new_only {
  position: relative;
  box-shadow: 1px 1px 4px 1px #ccc;
  p {
    position: absolute;
    bottom: -14px;
    width: 100%;
    background: #005b95b0;
    background-color: rgba(0, 91, 149, 0.69);
    font-size: 14px;
    color: #fff;
    padding: 15px 10px;
  }
}
.img_responsive {
  max-width: 100%;
}
.banner1{
  padding-left: 0;
  padding-right: 0;
}
.sectionHead{
  background-color: rgb(230, 135, 0);
  padding: 10px 20px;
  text-transform: uppercase;
  color: #f8f9fa
}
.banner2{
  padding-top: 16px;
}